import React from "react";
import { useContext, useState } from "react"; 
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft, faEnvelope, faUnlockAlt } from "@fortawesome/free-solid-svg-icons";
import { faFacebookF, faGithub, faTwitter } from "@fortawesome/free-brands-svg-icons";
import { Col, Row, Form, Card, Button, FormCheck, Container, InputGroup } from '@themesberg/react-bootstrap';
import { Link, Redirect } from 'react-router-dom';

import { Routes } from "../routes";
import BgImage from "../assets/img/illustrations/signin.svg";
import { signIn, AuthContext } from '../services/auth';
import { useFormState } from 'react-use-form-state';

export default () => {
  const [formState, { text, password }] = useFormState();
  const {username} = useContext(AuthContext);
  const [errorMessage, setErrorMessage] = useState(null);
  const handleSubmitAsync = async () => {
    try {
      await signIn(formState.values.username, formState.values.password);
    }
    catch(e) {
      try {
        if(e.response.status === 400) {
          setErrorMessage("Usuario/Password incorrecto");
        }
        else {
          setErrorMessage("Error desconocido");
        }
      }
      catch {
        setErrorMessage("Error desconocido");
      }

    }
  }
  const handleSubmit = (event) => {
    handleSubmitAsync();
    event.preventDefault();
  };
  console.log("username is");
  console.log(username);
  if (username && username !== "undefined") {
    return <Redirect path="/" />
  }

  return (
    <main>
      <section className="d-flex align-items-center my-5 mt-lg-6 mb-lg-5">
        <Container>
          <Row className="justify-content-center form-bg-image" style={{ backgroundImage: `url(${BgImage})` }}>
            <Col xs={12} className="d-flex align-items-center justify-content-center">
              <div className="bg-white shadow-soft border rounded border-light p-4 p-lg-5 w-100 fmxw-500">
                <div className="text-center text-md-center mb-4 mt-md-0">
                  <h3 className="mb-0">Ingreso a la plataforma</h3>
                </div>
                <form onSubmit={handleSubmit}>
                  <Form.Group id="email" className="mb-4" >
                    <Form.Label>Email</Form.Label>
                    <InputGroup >
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEnvelope} />
                      </InputGroup.Text>
                      <Form.Control autoFocus required {... text('username')} />
                    </InputGroup>
                  </Form.Group>
                  <Form.Group>
                    <Form.Group id="password" className="mb-4">
                      <Form.Label>Password</Form.Label>
                      <InputGroup>
                        <InputGroup.Text>
                          <FontAwesomeIcon icon={faUnlockAlt} />
                        </InputGroup.Text>
                        <Form.Control required {... password('password')} />
                      </InputGroup>
                    </Form.Group>
                  </Form.Group>
                  {
                    errorMessage &&
                    <div style={{color: 'red'}}>{errorMessage}</div>
                  }
                  <Button variant="primary" type="submit" className="w-100">
                    Ingresar
                  </Button>
                </form>
                <div className="d-flex justify-content-center align-items-center mt-4">
                  <span className="fw-normal">
                    No estás registrado? Contactá a tu administradorx
                  </span>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </main>
  );
};
