import React, { useState, useEffect, useContext } from 'react';
import { Route, Switch, Redirect } from "react-router-dom";
import { Routes } from "../routes";

// POSTA
import Vivienda from "./Vivienda";
import ViviendaSingle from "./ViviendaSingle";
import Signin from "./Signin";
import ViviendaContacto from './ViviendaContacto';

// components
import Sidebar from "../components/Sidebar";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import Preloader from "../components/Preloader";

import { AuthContext } from "../services/auth";
const RouteWithLoader = ({ component: Component, ...rest }) => {
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setLoaded(true), 1000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <Route {...rest} render={props => ( <> <Preloader show={loaded ? false : true} /> <Component {...props} /> </> ) } />
  );
};

const RouteWithSidebar = ({ component: Component, ...rest }) => {
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setLoaded(true), 1000);
    return () => clearTimeout(timer);
  }, []);

  const localStorageIsSettingsVisible = () => {
    return localStorage.getItem('settingsVisible') === 'false' ? false : true
  }

  const [showSettings, setShowSettings] = useState(localStorageIsSettingsVisible);

  const toggleSettings = () => {
    setShowSettings(!showSettings);
    localStorage.setItem('settingsVisible', !showSettings);
  }

  return (
    <Route {...rest} render={props => (
      <>
        <Preloader show={loaded ? false : true} />
        <Sidebar />

        <main className="content">
          <Navbar />
          <Component {...props} />
          <Footer toggleSettings={toggleSettings} showSettings={showSettings} />
        </main>
      </>
    )}
    />
  );
};

const requiresLogin = (Component) => {
  return (props) => {
    const {username} = useContext(AuthContext);
    console.log("in requires login username is");
    console.log(username);
    if(username === "undefined" || !username) {
      return <Redirect to={Routes.Signin.path} />
    }
    else {
      return <Component {...props} />;
    }
  }
}
export default () => {
  const [username, setUsername] = useState(null);

  const checkUserData = (check) => {
    const item = window.localStorage.getItem('username');
    console.log("got item");
    console.log(item);
    console.log("username was");
    console.log(username);
    //el uso del parametro check es porque, corrida adentro de esta funcion
    //no hay que llamar setUsername siempre porque genera un loop infinito
    //pero en la invocación del listener, el valor username no está disponible
    //y no hay que chequear en ese caso
    if(!check || item !== username) {
      setUsername(item);
    }
  }
  checkUserData(true);

  useEffect(() => {
    checkUserData();
    window.addEventListener('storage', () => checkUserData(false))
    return () => {
      window.removeEventListener('storage', () => checkUserData(false));
    }
  }, []);

  console.log("in homepage username is");
  console.log(username);
  return <AuthContext.Provider value={{username: username}}>
    <Switch>
      <RouteWithLoader exact path={Routes.Signin.path} component={Signin} />
      <RouteWithSidebar exact path={"/"} component={requiresLogin(Vivienda)} />
      <RouteWithSidebar exact path={Routes.Vivienda.path} component={requiresLogin(Vivienda)} />
      <RouteWithSidebar exact path={Routes.ViviendaSingle.path} component={requiresLogin(ViviendaSingle)} />
      <RouteWithSidebar exact path={Routes.ViviendaContacto.path} component={requiresLogin(ViviendaContacto)} />

      <Redirect to={Routes.NotFound.path} />
    </Switch>
  </AuthContext.Provider>;
};
