import axios from 'axios';
import baseUrl from './baseUrl';
import store from "store";
import { logout } from './auth';

const errorHandler = (error) => {
  if(error.response.status === 401 && localStorage.getItem('token') !== null){
    logout();
  }
};

const thisAxios = () => {
  const instance = axios.create({
    baseURL: baseUrl,
    headers: {
      "Authorization":
      "TOKEN " + localStorage.getItem('token')
    }
  });

  instance.interceptors.response.use((r) => r, (e) => {errorHandler(e); return Promise.reject(e);})
  return instance;
};

export const getViviendas = async () => {
  const result = await thisAxios().get('api/encuestas');
  return result.data;
}

export const getViviendaById = async (id) => {
  const result = await thisAxios().get('api/encuestas/' + id);
  return result.data;
}

export const getViviendaReal = (viviendaId) => {
  return new Promise ((resolve, reject) => {
    const result = store.get('real.' + viviendaId);
    resolve(result);
  });
}

export const setViviendaReal = (encuestaId, contacto) => {
  const data = {encuesta: encuestaId, ...contacto}
  return thisAxios().post('api/viviendas/', data).then( () => {
    store.set('real.' + encuestaId, contacto);
  })
}


export const getViviendaContacto = (viviendaId) => {
  return new Promise ((resolve, reject) => {
    const result = store.get('contacto.' + viviendaId);
    resolve(result);
  });
}

export const setViviendaContacto = (encuestaId, contactoHogares) => {

  return thisAxios().post('api/hogares/', {
    encuesta: encuestaId,
    hogares: contactoHogares
  }).then( () => {
    store.set('contacto.' + encuestaId, contactoHogares);
  })
}

