const DatosCantidadHogares = ({vivienda, onSubmit}) => {

  const [formState, {number}] = useFormState({
    cantidadHogares: 1
  })

  const submitHandler = (e) => {
    e.preventDefault()
    console.log("Cantidad de hogares: ", formState.values.cantidadHogares)
    onSubmit(formState.values.cantidadHogares)
  }

  return (
    <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
        <Form onSubmit={submitHandler}>
          <h5>Cantidad de hogares</h5>
          <Col md={4} className="mb-3">
              <Form.Group className="mb-3">
                <Form.Label>Ingrese cantidad de hogares en la vivienda</Form.Label>
                <Form.Control {...number('cantidadHogares')} />
              </Form.Group>
          </Col>
          <Col md={3} className="mb-3">
            <Button variant="primary" type="submit">Siguiente</Button>
          </Col>
        </Form>
      </Card.Body>
    </Card>
  )
}


const getHogares = (hogaresData, cantidadHogares) => {
  /*
  Acá hago toda la porquería
  */
  var ret = []
  const allKeys = Object.keys(hogaresData)
  for(var i = 0; i < cantidadHogares; ++i) {
    var keys = allKeys.filter(x => x.endsWith("_"+i))
    var obj = keys.reduce((res, key) => {

      let nice_key = key.slice(0, key.lastIndexOf("_"))
      res[nice_key] = hogaresData[key]
      return res
    }, {})
    ret = ret.concat(obj)
  }
  return ret
}




