export default () => {
  const { id } = useParams();
  const [cantidadHogares, setHogares] = useState(null)

  const { isLoading, data, error } = usePromise(async () => {
    const vivienda = await getViviendaById(id);
    const contacto = await getViviendaContacto(id);
    return {vivienda, contacto};
  });


  const actualizarCantidadHogares = (cant) => {
    setHogares(parseInt(cant))
  }

  console.log("Cantidad de hogares: ", cantidadHogares)
  console.log(typeof(cantidadHogares))
  return (
      <div>

          <h1 className="h2">Vivienda</h1>

          <div>
          {isLoading || error ? "Loading..." :
            <DatosCantidadHogares vivienda={data.vivienda} onSubmit={actualizarCantidadHogares}/>
            // <DatosContacto vivienda={data.vivienda} contacto={data.contacto} />
          }
          </div>

          <div>
          {
            isLoading || error || (cantidadHogares === null) ? "" :
              <DatosContacto vivienda={data.vivienda} contacto={data.contacto} cantidadHogares={cantidadHogares} />

          }
          </div>
      </div>

  );

};
