import React, { useState } from 'react';
import { Col, Row, Card, Table, Form, Button, Container, InputGroup } from '@themesberg/react-bootstrap';
import { useFormState } from 'react-use-form-state';
import provincias from './provincias';
import usePromise from "react-fetch-hook/usePromise";
import { getViviendas } from "../services/ViviendaService";
import { Link } from "react-router-dom";
import { RouteBuilder } from "../routes";

const renderRow = (row) => {
  return <tr key={row.id}>
    <td className="border-0 fw-bold">
      {row.id}
    </td>
    <td className="border-0">
      {row.provincia}
    </td>
    <td className="border-0">
      {row.departamento}
    </td>
    <td className="border-0">
      {row.calle}
    </td>
    <td className="border-0">
      {row.altura}
    </td>
    <td className="border-0">
      {row.descripcion}
    </td>
    <td>
      <Link to={RouteBuilder.viviendaSingle(row.id)} className="mb-3 w-100 btn btn-primary">
        Completar</Link>
    </td>
  </tr>;
};


const TablaEncuestas = ({encuestas}) => {
  return (
    <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
      <Row className="d-flex flex-wrap flex-md-nowrap align-items-center py-4">
        <Col className="d-block mb-4 mb-md-0">
          <h1 className="h2">Encuestas</h1>

          <Table responsive className="table-centered table-nowrap rounded mb-0">
            <thead className="thead-light">
              <tr>
                <th className="border-0">Id</th>
                <th className="border-0">Provincia</th>
                <th className="border-0">Departamento</th>
                <th className="border-0">Calle</th>
                <th className="border-0">Altura</th>
                <th className="border-0">Descripcion</th>
                <th className="border-0"></th>
              </tr>
            </thead>
            <tbody>

                {encuestas.map(renderRow)}
            </tbody>
          </Table>
          </Col>
        </Row>
      </Card.Body>
    </Card>
  )

}

const TablaHogares = ({encuestas}) => {

  var hogares = []

  for (var encuesta of encuestas) {
    for (var hogar of encuesta["hogares"]) {
      var ret = Object.assign({...encuesta}, hogar)
      delete ret.hogares
      ret["id_encuesta"] = encuesta["id"]
      console.log(ret)
      hogares = hogares.concat(ret)
    }
  }

  console.log(hogares)

  //console.log(hogares)
  return (
    <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
      <Row className="d-flex flex-wrap flex-md-nowrap align-items-center py-4">
        <Col className="d-block mb-4 mb-md-0">
          <h1 className="h2">Hogares</h1>

          <Table responsive className="table-centered table-nowrap rounded mb-0">
            <thead className="thead-light">
              <tr>
                <th className="border-0">Encuesta</th>
                <th className="border-0">Hogar</th>
                <th className="border-0">Provincia</th>
                <th className="border-0">Departamento</th>
                <th className="border-0">Calle</th>
                <th className="border-0">Altura</th>
                <th className="border-0">Descripcion</th>
                <th className="border-0">Nombre Pila</th>
                <th className="border-0"></th>
              </tr>
            </thead>
            <tbody>

                {hogares.map(hogar => (
                  <tr key={hogar.id}>
                    <td className="border-0 fw-bold">
                      {hogar.id_encuesta}
                    </td>
                    <td className="border-0 fw-bold">
                      {hogar.id}
                    </td>
                    <td className="border-0">
                      {hogar.provincia}
                    </td>
                    <td className="border-0">
                      {hogar.departamento}
                    </td>
                    <td className="border-0">
                      {hogar.calle}
                    </td>
                    <td className="border-0">
                      {hogar.altura}
                    </td>
                    <td className="border-0">
                      {hogar.descripcion}
                    </td>
                    <td className="border-0">
                      {hogar.nombre_contacto}
                    </td>
                    <td>
                      {/* <Link to={RouteBuilder.viviendaSingle(hogar.id)} className="mb-3 w-100 btn btn-primary">
                        Completar
                      </Link> */}
                    </td>
                </tr>
                ))}


            </tbody>
          </Table>
          </Col>
        </Row>
      </Card.Body>
    </Card>
  )

}


const RenderData = ({data}) => {
  // Las que no fueron completadas
  const encuestas = data.filter(x => !x["vivienda"])
  const hogares = data.filter(x => x["hogares"].length > 0)

  return (
    <div>
      <TablaEncuestas encuestas={encuestas} />
      <TablaHogares encuestas={hogares} />
    </div>
  );

}
export default () => {
  const {isLoading, data, error} = usePromise(getViviendas);

  return (isLoading || error ? <div>Loading...</div> : <RenderData data={data} />)
}
