import React, { useState } from 'react';
import { Col, Row, Card, Form, Button, Container, InputGroup } from '@themesberg/react-bootstrap';
import PrettyPrint from './PrettyPrint';
import { useFormState } from 'react-use-form-state';
import provincias from './provincias';
import { useParams } from "react-router-dom";
import { getViviendaById, getViviendaReal, setViviendaReal } from "../services/ViviendaService";
import usePromise from "react-fetch-hook/usePromise";
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';
import { RouteBuilder } from "../routes";

const ConfirmarDatoVivienda = ({initialVivienda, viviendaReal}) => {
  const keys = [
    "calle", "altura", "piso", "depto", "descripcion",
  ]

  const datos = viviendaReal || initialVivienda

  const datosVivienda = keys.reduce((obj, key) => {
      obj[key] = datos[key];
      return obj
  }, {})

  const [formState, { text, checkbox }] = useFormState(
    datosVivienda
  );

  const [submitted, setSubmitted] = useState(false);

  const submitDatos = async (event) => {
    event.preventDefault();
    await setViviendaReal(initialVivienda.id, formState.values);
    setSubmitted(true);
  }

  if(submitted) {
    return <Redirect to={RouteBuilder.viviendaContacto(initialVivienda.id)} />
  }
  return (
    <Form onSubmit={submitDatos}>

      <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
        <h5>Confirmar datos vivienda</h5>
        <Row className="d-flex flex-wrap flex-md-nowrap align-items-center py-4">
          <Col md={4} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Calle</Form.Label>
              <Form.Control {...text('calle')} />
            </Form.Group>
          </Col>
          <Col md={2} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Altura</Form.Label>
              <Form.Control {...text('altura')} />
            </Form.Group>
          </Col>
          <Col md={2} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Piso</Form.Label>
              <Form.Control {...text('piso')} />
            </Form.Group>
          </Col>
          <Col md={2} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Dpto</Form.Label>
              <Form.Control {...text('depto')} />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col md={10} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Descripcion</Form.Label>
              <Form.Control {...text('descripcion')}/>
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col md={4} className="mb-3">
            <Form.Group className="mb-3">
              <Form.Label>Reemplazo</Form.Label>
              <Form.Check {...checkbox('es_reemplazo')}/>
            </Form.Group>
          </Col>
        </Row>
        </Card.Body>
      </Card>
      <Row>
        <Col md={3} className="mb-3">
          <Button variant="primary" type="submit">Siguiente</Button>
        </Col>
      </Row>
      <PrettyPrint formState={formState} />
    </Form>
  );
}

export default () => {
  const { id } = useParams();
  const { isLoading, data } = usePromise(async() => {
    const vivienda = await getViviendaById(id);
    const viviendaReal = await getViviendaReal(id);
    return {vivienda, viviendaReal}
  });


  return (
      <div>

          <h1 className="h2">Vivienda</h1>
          {isLoading ? "Loading..." : <ConfirmarDatoVivienda initialVivienda={data.vivienda} viviendaReal={data.viviendaReal} />}
      </div>

  );

};
